@import "../../../mixins.scss";

.herobanner {
    width: 100%;
    height: 450px;
    background: var(--black) ;
    display: flex;
    align-items: center;
    position: relative;
    @include md {
        height: 700px;
    }
    .backdrop-img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0.5;
        overflow: hidden;
        .lazy-load-image-background {
            width: 100%;
            height: 100%;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center;
            }
        }
    }
    .opacity-layer {
        width: 100%;
        height: 250px;
        background: linear-gradient(180deg , rgba(4,21,45,0) 0% ,#04152d 79.17%);
        position: absolute;
        bottom: 0;
        left: 0;
    }
    .home__content{
        display: flex;
        flex-direction: column;
        align-items: center;
        color: white;
        text-align: center;
        position: relative;
        max-width: 800px;
        margin: 0 auto;
        .title {
            font-size:50px;
            font-weight: 700;
            @include md {
                margin-bottom: 0;
                font-size: 90px;
            }
        }
        .subtitle {
            font-size: 18px;
            font-weight: 500;
            margin-bottom: 40px;
            @include md {
                font-size: 24px;
            }
        }
        .search{
            display: flex;
            align-items: center;
            width: 100%;
            input {
                width: calc(100% - 100px);
                height: 50px;
                background-color: white;
                outline: 0;
                border: 0;
                border-radius: 30px 0 0 30px;
                padding: 0 15px;
                font-size: 14px;
                @include md {
                    width: calc(100% - 150px);
                    height: 60px;
                    font-size: 20px;
                    padding: 0 30px;
                }
            }
            button {
                width: 100px;
                height: 50px;
                background: var(--gradient);
                color: white;
                outline: 0;
                border: 0;
                border-radius: 0 30px 30px 0;
                font-size: 16px;
                @include md {
                    width: 150px;
                    height: 60px;
                    font-size: 18px;
                }
            }
        }
    }
}
@media screen and (max-width:600px) {
    .herobanner .home__content .search input {
        height: 40px;
    }
    .herobanner .home__content .search button {
        height: 40px;
    }
}
@media screen and (max-width:400px) {
    .herobanner {
        height: 350px;
    }
    .herobanner .home__content .title {
        font-size: 30px;
    }
    .herobanner .home__content .subtitle {
        font-size: 15px;
        margin-bottom: 20px;
    }
}